import cpimg from '@/components/cpimg.vue';
import wPicker from '@/components/w-picker/w-picker.vue';
import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: { wPicker, uniIcon, cpimg },
	props: {
		text: {
			default: '',
			type: String
		},
		disabled:{
			default: false,
			type: Boolean
		},
		maxlength:{
			default: 140,
			type: Number
		},
		textAlign: {
			default: 'left',
			type: String
		},
		mode: {
			default: 'default',
			type: String
		},
		model: {
			default: '',
			type: String
		},
		placeholder: {
			default: '',
			type: String
		},
		textareaLength: {
			type: Number,
			default: 500
		},
		dataText: {
			default: '',
			type: String || Number
		},
		hasR: {
			default: false,
			type: Boolean
		},
		showRightText: {
			default: false,
			type: Boolean
		},
		rightText: {
			default: '',
			type: String
		},
		value: {
			default: '',
			type: String
		},
		name: {
			default: '',
			type: String
		},
		icon: {
			type: String,
			default: 'arrowright'
		},
		preview: {
			type: Boolean,
			default: true
		},
		// selector
		startYear: {
			type: String,
			default: '1949'
		},
		endYear: {
			type: String,
			default: '2030'
		},
		startHour: {
			type: String,
			default: '8'
		},
		endHour: {
			type: String,
			default: '20'
		},
		minuteStep: {
			type: String,
			default: '5'
		},
		afterStep: {
			type: String,
			default: '30'
		},
		editImg: {
			type: Boolean,
			default: false
		},
		imgList: {
			type: Array,
			default: () => {
				return [];
			}
		},
		imgNumber: {
			type: Number,
			default: 9
		},
		dataType: {
			default: '',
			type: String
		},
		noP: {
			default: false,
			type: Boolean
		},
		initIndex: {
			type: Number,
			default: 0
		},
		selectList: {
			type: Array,
			default: function() {
				return [{ label: '男', value: '0' }, { label: '女', value: '1' }];
			}
		},

		// input type
		inputType: {
			default: 'text',
			type: String
		},
		// 不为空 *
		hasX: {
			default: false,
			type: Boolean
		},
		useReg: {
			default: false,
			type: Boolean
		},

		regText: {
			type: String,
			default: ''
		}
	},
	created() {
		this.ImgNumber = this.imgNumber - this.copyimgList.length;
	},
	watch: {
		initIndex(val, oldval) {
			this.tabIndex = val;
		},
		imgList(val, oldval) {
			this.copyimgList = val;
		},
		value(val) {
			this.inputValue = val;
		}
	},
	data() {
		return {
			ImgNumber: '',
			inputValue: this.value,
			staticColor: 'default',
			selectorD: [this.initIndex],
			defaultVal2: [0, 1, 3],
			defaultVal3: [0, 0, 0],
			defaultVal4: [0, 0, 0, 0, 0, 0],
			tabIndex: this.initIndex,
			copyimgList: this.imgList,

			textareaVal: ''
		};
	},
	methods: {
		// 点击右侧事件
		rightClick() {
			this.$emit('handRight');
		},
		// 图片压缩
		cpimgOk(file) {
			file.map(item => {
				this.copyimgList.push(item);
			});
			this.ImgNumber = this.imgNumber - this.copyimgList.length;
			uni.hideLoading();
			this.$emit('getImg', this.copyimgList);
		},
		cpimgErr(e) {
			this.$emit('getImgErr', e);
		},
		// 图片压缩
		// 删除图片
		clearImg(l, i) {
			var length = l - 1;
			this.copyimgList.splice(i, 1);
			this.ImgNumber = this.imgNumber - this.copyimgList.length;
			this.$emit('clearImg', length);
		},
		// 删除图片

		textFocus() {
			this.staticColor = 'default';
			this.$emit('focus');
		},
		textBlur(event) {
			if (this.useReg) {
				// 不满足正则
				this.staticColor = 'err';
				uni.showToast({ title: this.regText, icon: 'none' });
			}
			this.$emit('blur', event.target.value);
		},
		// input 输入框输入事件
		handleChange(event) {
			this.$emit('input', event.target.value);
		},
		handtextarea(event) {
			this.textareaVal = event.target.value;
			this.$emit('input', event.target.value);
		},
		toggleTabClick() {
			this.$emit('handClick');
		},
		toggleTabImg() {
			console.log(1);
		},
		toggleTabGetImg() {
			if (!this.editImg) {
				return;
			}
			if (this.ImgNumber < 0 || this.imgNumber <= this.copyimgList.length) {
				uni.showToast({ title: 'none', icon: 'none' });
				return;
			}
			this.$refs.cpimgs._changImg();
		},
		showPreview(index) {
			if (!this.preview) {
				return;
			}
			uni.previewImage({
				urls: this.copyimgList,
				indicator: 'number',
				current: index,
				longPressActions: {
					itemList: ['保存图片'], //, '收藏','发送给朋友',
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		toggleTabSelector() {
			this.$refs.selector.show();
		},
		toggleTabDate() {
			this.$refs.date.show();
		},
		toggleTabTime() {
			this.$refs.time.show();
		},
		toggleTabDateTime() {
			this.$refs.dateTime.show();
		},
		toggleTabRange() {
			this.$refs.range.show();
		},
		toggleTabYearMonth() {
			this.$refs.yearMonth.show();
		},
		toggleTabRegion() {
			this.$refs.region.show();
		},
		toggleTabLimit() {
			this.$refs.limit.show();
		},

		onConfirm(val) {
			this.tabIndex = val.checkArr.index;
			this.$emit('handSelect', val);
		},
		onConfirm2(val) {
			this.$emit('handDate', val);
		},
		onConfirm3(val) {
			this.$emit('handTime', val);
		},
		onConfirm4(val) {
			this.$emit('handDateTime', val);
		},
		onConfirm5(val) {
			this.$emit('handRange', val);
		},
		onConfirm6(val) {
			this.$emit('handYearMonth', val);
		},
		onConfirm7(val) {
			this.$emit('handRegion', val);
		},
		onConfirm8(val) {
			this.$emit('handLimit', val);
		}
	}
};
